import { doRequest } from 'tools/request';

import { DGApiResponse, isErrorResponse } from './common';

const apiKey = 'ruvqwj0703';

const apiPrefix = 'https://catalog.api.2gis.ru';

export const doDGApiRequest = async <Params, ResponseType>(url: string, params: Params): Promise<ResponseType> => {
    const response = await doRequest<DGApiResponse<ResponseType>>(`${apiPrefix}${url}`, {
        key: apiKey,
        ...params,
    });

    if (isErrorResponse(response)) {
        throw new Error(response.meta.error.message);
    }

    return response.result;
};
