import { createAction } from 'tools/create-action';
import { DGSuggest } from 'tools/2gis/api/suggest';

export const addressChangeInputValueAction = (payload: string) => createAction('address/CHANGE_INPUT_VALUE', payload);

export const addressSetSuggestsAction = (payload: DGSuggest[]) => createAction('address/SET_SUGGESTS', payload);

export const addressSelectSuggestItemAction = (payload: number) => createAction('address/SELECT_SUGGEST_ITEM', payload);

export const addressClickSuggestItemAction = (payload: string) => createAction('address/CLICK_SUGGEST_ITEM', payload);
