interface Fn {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    (...args: any): any;
}

export const memoize = <F extends Fn>(fn: F): F => {
    const store = new Map<string, ReturnType<F>>();

    return ((...args) => {
        const id = JSON.stringify(args);

        if (!store.has(id)) {
            store.set(id, fn(...args));
        }

        return store.get(id);
    }) as F;
};
