import { memoize } from 'tools/memoize';

import searchRegionData from '../../generated/geometry/search-region-data.wkt.json';

import { getSuggests, DGSuggest } from './api/suggest';
import { DGLocale } from './api/common';

const regionData = searchRegionData.data as Record<string, string>;
const regionList = searchRegionData.regions;

const memoizedGetSuggests = memoize(getSuggests);

const isPointInBBox = (point: [number, number], bbox: number[]): boolean => {
    // prettier-ignore
    return (
        point[0] >= bbox[0] &&
        point[1] >= bbox[1] &&
        point[0] <= bbox[2] &&
        point[1] <= bbox[3]
    );
};

const getPolygonList = memoize((lon: number, lat: number) => {
    const point: [number, number] = [lon, lat];

    return [...regionList]
        .sort((a, b) => Number(isPointInBBox(point, a.bbox)) - Number(isPointInBBox(point, b.bbox)))
        .map((item) => item.name);
});

interface GetSuggestsListParams {
    q: string;
    locale: DGLocale;
    longitude: string;
    latitude: string;
}

export const getSuggestsList = async ({ q, locale, longitude, latitude }: GetSuggestsListParams) => {
    const polygonList = getPolygonList(parseFloat(longitude), parseFloat(latitude));

    const suggestList = await Promise.all(
        polygonList.map((polygonName) =>
            memoizedGetSuggests({
                q,
                locale,
                polygon: regionData[polygonName] || '',
                fields: ['items.address', 'items.point'],
                suggestType: 'address',
                type: ['building', 'street'],
            })
                .then((response) => response.items)
                .catch(() => []),
        ),
    ).then((responseList) => responseList.flat());

    const suggestMap = suggestList.reduce((map, suggest) => {
        map.set(suggest.id, suggest);

        return map;
    }, new Map<string, DGSuggest>());

    return [...suggestMap.values()];
};
