/* eslint-disable @typescript-eslint/camelcase */
import { doDGApiRequest } from './doApiRequest';
import { DGLocale, DGListResult, DGLatLng } from './common';

type DGGetSuggestsParams = {
    locale: DGLocale;
    q: string;
    fields: string[];
    suggestType: string;
    type: string[];
    polygon: string;
};

interface DGApiGetSuggestsParams {
    locale: DGLocale;
    q: string;
    fields: string;
    suggest_type: string;
    type: string;
    polygon: string;
}

export interface DGSuggest {
    id: string;
    full_name: string;
    name: string;
    search_attributes: {
        suggested_text: string;
    };
    type: string;
    point?: DGLatLng;
}

export interface DGStreetSuggest extends DGSuggest {
    type: 'street';
}

export function isStreetSuggest(suggest: DGSuggest): suggest is DGStreetSuggest {
    return suggest.type === 'street';
}

interface DGBuildingAddressComponent {
    number: string;
    street: string;
    street_id: string;
    type: 'street_number';
}

interface DGBuildingAddress {
    building_id: string;
    components: DGBuildingAddressComponent[];
    postcode: string;
}

export interface DGBuildingSuggest extends DGSuggest {
    type: 'building';
    address_name: string; // Кутузовский проспект, 43 ст2
    purpose_name: string; // Жилой дом с административными помещениями
    address: DGBuildingAddress;
    point: DGLatLng;
}

export function isBuildingSuggest(suggest: DGSuggest): suggest is DGBuildingSuggest {
    return suggest.type === 'building';
}

interface DGGetSuggestsResult {
    total: number;
    items: DGSuggest[];
}

const convertParams = ({ fields, type, suggestType, ...params }: DGGetSuggestsParams): DGApiGetSuggestsParams => ({
    ...params,
    fields: fields.join(','),
    type: type.join(','),
    suggest_type: suggestType,
});

const suggestsEndpointPath = '/3.0/suggests';

export const getSuggests = async (params: DGGetSuggestsParams): Promise<DGGetSuggestsResult> => {
    if (!params.q) {
        return { total: 0, items: [] };
    }

    return doDGApiRequest<DGApiGetSuggestsParams, DGListResult<DGSuggest>>(suggestsEndpointPath, convertParams(params));
};
